/* Dm Sans Start */

/* Regular */
@font-face {
    font-family: 'DM Sans';
    src: url('../fonts/DM_Sans/DMSans-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

/* Regular Italic */
@font-face {
    font-family: 'DM Sans';
    src: url('../fonts/DM_Sans/DMSans-Italic.ttf') format('truetype');
    font-weight: normal;
    font-style: italic;
}

/* Medium */
@font-face {
    font-family: 'DM Sans';
