}

/* Bold */
@font-face {
    font-family: 'DM Sans';
    src: url('../fonts/DM_Sans/DMSans-Bold.ttf') format('truetype');
    font-weight: 700;
    font-style: normal;
}

/* Bold Italic */
@font-face {
    font-family: 'DM Sans';
    src: url('../fonts/DM_Sans/DMSans-BoldItalic.ttf') format('truetype');
    font-weight: 700;
    font-style: italic;
}

/* Dm Sans End */

